function openMenu() {
  const mobileMenu = document.querySelector('.mobile-menu-wrapper');
  const links = document.querySelectorAll('.nav-link');
  const btnOpenMenu = document.querySelector('.burger-menu');
  const btnCloseMenu = document.querySelector('.burger-menu-close');

  btnOpenMenu.addEventListener('click', () => {
    mobileMenu.classList.toggle('is-open');
  });

  btnCloseMenu.addEventListener('click', () => {
    mobileMenu.classList.toggle('is-open');
  });

  links.forEach(link =>
    link.addEventListener('click', () => {
      mobileMenu.classList.remove('is-open');
    })
  );
}

openMenu();

function changeActiveLink() {
  const links = document.querySelectorAll('.nav-link');

  window.addEventListener('load', () => {
    const currentPage = '.' + window.location.pathname; // Ð½Ð° Ð¶Ð¸Ð²Ñ–Ð¹ ÑÑ‚Ð¾Ñ€Ñ–Ð½Ñ†Ñ– Ð½Ð° Ð³Ñ–Ñ‚Ñ– Ð¿Ñ€Ð¾ÑÑ‚Ð¾ Ð¿Ñ€Ð°Ñ†ÑŽÑ” window.location.pathname

    // Ð½Ð° Ð¶Ð¸Ð²Ñ–Ð¹ ÑÑ‚Ð¾Ñ€Ñ–Ð½Ñ†Ñ– Ð½Ð° Ð³Ñ–Ñ‚Ñ– Ð¼Ð°Ñ” ÑÐ¿Ñ€Ð°Ñ†ÑŽÐ²Ð°Ñ‚Ð¸ Ð²Ð°Ñ€Ñ–Ð°Ð½Ñ‚: currentPage === '/fitnes-app/'
    if (currentPage === './') {
      links[0].classList.add('active-link');
    } else {
      links.forEach(link => {
        if (link.getAttribute('href') === currentPage) {
          link.classList.add('active-link');
        }
      });
    }
  });
}

changeActiveLink();
